import React from "react";
import { Box } from "../models/Box";

interface SquareProps {
  i: string;
  squareClick: () => void;
  b: Box;
}

export const Square = (props: SquareProps) => {
  const { i, squareClick, b } = props;
  const handleClick = () => {
    squareClick();
  };
  return (
    <button className="square" onClick={handleClick}>
      {b[Number(i)]}
    </button>
  );
};
